<template lang="">
  <el-dialog v-model="display" destroy-on-close>
    <template #header>
      <span> </span>
    </template>
    <el-form :model="form">
      <el-form-item label=" ">
        <el-input v-model="form.title"></el-input>
      </el-form-item>
      <el-form-item label=" ">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item label=" ">
        <addMemberV2 @savePart="savePart" :parentData="form.member" />
        <showMemberV2 :parentData="form.member" />
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="form.daysOfWeek">
          <el-checkbox-button
            v-for="button in day"
            :key="button"
            :label="button"
          >
            {{ button }}
          </el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label=" ">
        <el-switch
          v-model="form.endRecur"
          active-text=" "
          inactive-text=""
        />
        <el-date-picker
          v-if="form.endRecur"
          v-model="form.endDate"
          type="date"
          placeholder="   ."
          style="width: 100%"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">Submit</el-button>
        <el-button @click="handleClose">Cancel</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
import addMemberV2 from "../project/addMemberV2.vue";
import showMemberV2 from "../project/showMemberV2.vue";
import moment from "moment";
export default {
  name: "arrangeEvent",
  components: {
    addMemberV2,
    showMemberV2,
  },
  data() {
    return {
      form: {
        title: "",
        member: [],
        desc: "",
        daysOfWeek: [],
        endRecur: false,
        endDate: "",
      },
      day: ["", "", "", "", "", "", ""],
      display: false,
    };
  },
  props: {
    clicked: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    savePart(data) {
      this.form.member = data;
    },
    onSubmit() {
      //this.form.day ->0, ->6  .
      const newDay = this.form.daysOfWeek.map((day) => {
        return this.day.indexOf(day);
      });
      //  this.form.day .
      this.form.daysOfWeek = newDay;
      if (this.form.endRecur) {
        this.form.endRecur = moment(this.form.endDate).format("YYYY-MM-DD");
        delete this.form.endDate;
      } else {
        delete this.form.endRecur;
      }
      const newForm = {
        ...this.form,
        groupId: this.form.title,
        startRecur: moment().format("YYYY-MM-DD"),
      };
      console.log(newForm, "emit");
      this.$emit("saveEvent", newForm);
      this.handleClose();
    },
    handleClose() {
      (this.form = {
        title: "",
        member: [],
        desc: "",
        day: [],
        endRecur: false,
        endDate: "",
      }),
        (this.display = false);
    },
  },
  watch: {
    //Date   display true .
    clicked() {
      this.display = true;
    },
  },
};
</script>
<style lang=""></style>
